"use client";

import { useActionState, useEffect, useState } from "react";
import { useFormStatus } from "react-dom";
import { checkSymptoms, explainCureAction } from "@/lib/actions";
import { Button } from "@/components/ui/button";
import { Textarea } from "@/components/ui/textarea";
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
  DialogTrigger,
  DialogFooter,
} from "@/components/ui/dialog";
import { Badge } from "@/components/ui/badge";
import { Loader2, Bot, AlertTriangle, Lightbulb, Sparkles, Stethoscope, ArrowRight } from "lucide-react";
import Link from "next/link";
import { AISymptomCheckerOutput } from "@/ai/flows/ai-symptom-checker";

function SubmitButton() {
  const { pending } = useFormStatus();
  return (
    <Button type="submit" className="w-full md:w-auto" disabled={pending}>
      {pending && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
      Analyze Symptoms
    </Button>
  );
}

function ExplainCureButton({symptoms, cure}: {symptoms: string, cure: string}) {
    const { pending } = useFormStatus();
    
    return (
        <Button variant="secondary" type="submit" disabled={pending}>
            {pending && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
            Explain Cure
        </Button>
    )
}

function CureExplanationDialog({ symptoms, cure, diagnosis }: { symptoms: string, cure: string, diagnosis: string }) {
    const [open, setOpen] = useState(false);
    const [state, action] = useActionState(explainCureAction, { data: null, error: null });

    useEffect(() => {
        if(state.data?.explanation) {
            // handle success
        }
    }, [state.data])

    return (
        <Dialog open={open} onOpenChange={setOpen}>
            <DialogTrigger asChild>
                <Button variant="link" className="p-0 h-auto">
                    Learn more
                </Button>
            </DialogTrigger>
            <DialogContent className="sm:max-w-md">
                <form action={action}>
                    <input type="hidden" name="symptoms" value={symptoms} />
                    <input type="hidden" name="cure" value={cure} />

                    <DialogHeader>
                        <DialogTitle>Explanation for: {cure}</DialogTitle>
                        <DialogDescription>
                            AI-generated explanation for the suggested cure based on your symptoms for {diagnosis}.
                        </DialogDescription>
                    </DialogHeader>
                    <div className="py-4 space-y-4">
                        {state.data?.explanation ? (
                           <div className="prose prose-sm text-sm text-muted-foreground max-h-[40vh] overflow-y-auto">
                             <p>{state.data.explanation}</p>
                           </div>
                        ) : state.error ? (
                            <Alert variant="destructive">
                                <AlertTriangle className="h-4 w-4" />
                                <AlertTitle>Error</AlertTitle>
                                <AlertDescription>{state.error}</AlertDescription>
                            </Alert>
                        ) : (
                            <div className="text-sm text-muted-foreground">Click the button below to generate an explanation.</div>
                        )}
                    </div>
                    <DialogFooter>
                       {!state.data?.explanation && <ExplainCureButton symptoms={symptoms} cure={cure} />}
                        <Button type="button" variant="outline" onClick={() => setOpen(false)}>Close</Button>
                    </DialogFooter>
                </form>
            </DialogContent>
        </Dialog>
    )
}

function SeverityBadge({ severity }: { severity: AISymptomCheckerOutput["severity"] }) {
  const severityStyles = {
    low: "bg-green-100 text-green-800 border-green-200",
    moderate: "bg-yellow-100 text-yellow-800 border-yellow-200",
    high: "bg-red-100 text-red-800 border-red-200",
  };
  return <Badge className={`${severityStyles[severity]} capitalize`}>{severity}</Badge>;
}

export default function AiHelpPage() {
  const [state, action] = useActionState(checkSymptoms, { data: null, error: null });
  const [symptoms, setSymptoms] = useState('');

  return (
    <div className="max-w-4xl mx-auto space-y-8">
      <Card>
        <CardHeader>
          <div className="flex items-center gap-3">
            <Bot className="h-8 w-8 text-primary" />
            <div>
              <CardTitle>AI Symptom Checker</CardTitle>
              <CardDescription>
                Describe your symptoms, and our AI will provide a preliminary analysis.
              </CardDescription>
            </div>
          </div>
        </CardHeader>
        <form action={action}>
          <CardContent>
            <Textarea
              name="symptoms"
              placeholder="e.g., I have a persistent cough, a mild fever, and a headache for the past 3 days..."
              className="min-h-[120px]"
              onChange={(e) => setSymptoms(e.target.value)}
              required
            />
            {state.error && (
              <p className="mt-2 text-sm text-destructive">{state.error}</p>
            )}
          </CardContent>
          <CardFooter className="flex justify-end">
            <SubmitButton />
          </CardFooter>
        </form>
      </Card>

      {state.data && (
        <Card className="bg-gradient-to-br from-primary/10 to-accent/10">
          <CardHeader>
            <div className="flex items-center gap-3">
              <Sparkles className="h-8 w-8 text-primary" />
              <CardTitle>AI Analysis Report</CardTitle>
            </div>
          </CardHeader>
          <CardContent className="space-y-6">
            <div className="space-y-2">
                <h3 className="font-semibold text-lg flex items-center gap-2"><Lightbulb className="h-5 w-5"/>Potential Diagnosis</h3>
                <p className="text-muted-foreground">{state.data.diagnosis}</p>
            </div>
            <div className="space-y-2">
                <h3 className="font-semibold text-lg flex items-center gap-2"><Stethoscope className="h-5 w-5"/>Suggested Cure</h3>
                <div className="flex items-center justify-between">
                    <p className="text-muted-foreground">{state.data.cure}</p>
                    <CureExplanationDialog symptoms={symptoms} cure={state.data.cure} diagnosis={state.data.diagnosis}/>
                </div>
            </div>
            
            <div className="space-y-4 rounded-lg border p-4">
                 <h3 className="font-semibold text-lg flex items-center gap-2"><AlertTriangle className="h-5 w-5"/>Severity Assessment</h3>
                <div className="flex items-center justify-between">
                    <p className="text-muted-foreground">The AI assesses the severity as:</p>
                    <SeverityBadge severity={state.data.severity} />
                </div>
                {state.data.severity === 'high' && <Alert variant="destructive">
                    <AlertTriangle className="h-4 w-4" />
                    <AlertTitle>Immediate Action Recommended</AlertTitle>
                    <AlertDescription>Your symptoms suggest a high severity condition. Please consider visiting a hospital or contacting emergency services.</AlertDescription>
                </Alert>}
                {state.data.severity === 'moderate' && <Alert>
                    <AlertTriangle className="h-4 w-4" />
                    <AlertTitle>Doctor Consultation Recommended</AlertTitle>
                    <AlertDescription>We recommend consulting a doctor to get a professional diagnosis. You can find recommended specialists below.</AlertDescription>
                </Alert>}
            </div>

            {state.data.recommendedDoctors && state.data.recommendedDoctors.length > 0 && (
              <div className="space-y-2">
                <h3 className="font-semibold text-lg">Recommended Specialists</h3>
                <div className="flex flex-wrap gap-2">
                    {state.data.recommendedDoctors.map(doctor => (
                        <Badge key={doctor} variant="secondary">{doctor}</Badge>
                    ))}
                </div>
                <div className="pt-2">
                    <Button asChild>
                        <Link href="/dashboard/doctors">Find a Specialist <ArrowRight className="ml-2 h-4 w-4"/></Link>
                    </Button>
                </div>
              </div>
            )}
          </CardContent>
        </Card>
      )}

      <Alert>
        <Bot className="h-4 w-4" />
        <AlertTitle>Disclaimer</AlertTitle>
        <AlertDescription>
          The AI Symptom Checker is for informational purposes only and does not constitute medical advice. Always consult with a qualified healthcare professional for diagnosis and treatment.
        </AlertDescription>
      </Alert>
    </div>
  );
}
